}

#courses {
  border-collapse: collapse;
  /*border: 1px solid #ddd;*/
  border-radius: 40px;
  max-height:50px;
}

#courses td,
#courses th {
  border: 1px solid #ddd;
  padding: 12px;

}

#courses tr:hover {
  background-color: #ddd;
}

#courses th {
  padding: 10px;
  text-align: center;
  background-color: #4d33e4;
  color: white;
  border: blue;
  
}

.opration {
  text-align: center;
}


.tableFixHead          { 
  overflow-y: auto; height: 480px; 
}
.tableFixHead thead th { 
  position: -webkit-sticky; 
  position: sticky; top: 0; 
  z-index: 10000 !important;
}

.modal{
   z-index: 100001 !important;
}

